.panelGradiente {
  height: 20vh;
  text-align: center;
  background-image: linear-gradient(
    to top,
    rgba(34, 168, 210, 0.2) 115%,
    rgba(255, 255, 255, 0) -21%
  );
}
.Rectangle {
  width: 300px;
  height: 86px;
  border-radius: 4px;
  box-shadow: 0 1px 9px 0 rgba(56, 56, 56, 0.2);
  background-image: linear-gradient(to bottom, #fefefe, var(--white));
  text-align: center;
}

.panelVioleta {
  height: 20vh;
  background-color: #783183;
}
